import popup from '../uni-popup/popup.js'
import {
initVueI18n
} from '@dcloudio/uni-i18n'
import messages from '../uni-popup/i18n/index.js'
const {	t	} = initVueI18n(messages)
/**
 * PopUp 弹出层-对话框样式
 * @description 弹出层-对话框样式
 * @tutorial https://ext.dcloud.net.cn/plugin?id=329
 * @property {String} value input 模式下的默认值
 * @property {String} placeholder input 模式下输入提示
 * @property {String} type = [success|warning|info|error] 主题样式
 *  @value success 成功
 * 	@value warning 提示
 * 	@value info 消息
 * 	@value error 错误
 * @property {String} mode = [base|input] 模式、
 * 	@value base 基础对话框
 * 	@value input 可输入对话框
 * @property {String} content 对话框内容
 * @property {Boolean} beforeClose 是否拦截取消事件
 * @event {Function} confirm 点击确认按钮触发
 * @event {Function} close 点击取消按钮触发
 */

export default {
	name: "uniPopupDialog",
	mixins: [popup],
	emits:['confirm','close'],
	props: {
		value: {
			type: [String, Number],
			default: ''
		},
		placeholder: {
			type: [String, Number],
			default: ''
		},
		type: {
			type: String,
			default: 'error'
		},
		mode: {
			type: String,
			default: 'base'
		},
		title: {
			type: String,
			default: ''
		},
		content: {
			type: String,
			default: ''
		},
		beforeClose: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			dialogType: 'error',
			focus: false,
			val: ""
		}
	},
	computed: {
		okText() {
			return t("uni-popup.ok")
		},
		cancelText() {
			return t("uni-popup.cancel")
		},
		placeholderText() {
			return this.placeholder || t("uni-popup.placeholder")
		},
		titleText() {
			return this.title || t("uni-popup.title")
		}
	},
	watch: {
		type(val) {
			this.dialogType = val
		},
		mode(val) {
			if (val === 'input') {
				this.dialogType = 'info'
			}
		},
		value(val) {
			this.val = val
		}
	},
	created() {
		// 对话框遮罩不可点击
		this.popup.disableMask()
		// this.popup.closeMask()
		if (this.mode === 'input') {
			this.dialogType = 'info'
			this.val = this.value
		} else {
			this.dialogType = this.type
		}
	},
	mounted() {
		this.focus = true
	},
	methods: {
		/**
		 * 点击确认按钮
		 */
		onOk() {
			if (this.mode === 'input'){
				this.$emit('confirm', this.val)
			}else{
				this.$emit('confirm')
			}
			if(this.beforeClose) return
			this.popup.close()
		},
		/**
		 * 点击取消按钮
		 */
		closeDialog() {
			this.$emit('close')
			if(this.beforeClose) return
			this.popup.close()
		},
		close(){
			this.popup.close()
		}
	}
}
